import { FC } from 'react';
import styled from 'styled-components';
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import * as yup from 'yup';
import {
  LinkedInIcon,
  MailIcon,
  MessageIcon,
  PurposeIcon,
  TwitterIcon,
  UserIcon,
  YouTubeIcon,
} from '../icons';
import { StyledButton, StyledText } from './StyledComponents';
import HookFooterInput from './hookForm/HookFooterInput';

interface FormTypes {
  message: string;
  name: string;
  purpose: string;
  email: string;
}

const schema = yup.object().shape({
  email: yup
    .string()
    .email('Email is not valid!')
    .required('email is required!'),
  name: yup.string().required('Name is required!'),
  purpose: yup.string().required('Purpose is required!'),
  message: yup.string().required('Message is required!'),
});

const defaultValues = {
  email: '',
  name: '',
  purpose: '',
  message: '',
};

const Footer: FC = () => {
  const {
    handleSubmit,
    control,
    formState: { errors },
    reset,
  } = useForm<FormTypes>({
    resolver: yupResolver(schema),
    mode: 'onChange',
    defaultValues,
  });

  const submitForm = (formVales: FormTypes) => {
    console.log(formVales);
    reset(defaultValues);
  };

  return (
    <div className="flex flex-col">
      <MainContainer>
        <Overlay />
        <div className="w-full h-full container mx-auto py-20">
          <div className="flex flex-wrap">
            <div className="w-full xl:w-8/12 flex">
              <div className="w-1/5">
                <StyledText
                  className="mb-6"
                  fs={20}
                  fw={800}
                  lh={36}
                  ls={2}
                  color="#fff"
                >
                  Pages
                </StyledText>
                <StyledText
                  className="mb-px"
                  fs={18}
                  fw={400}
                  lh={28}
                  color="#fff"
                >
                  Home
                </StyledText>
                <StyledText
                  className="mb-px"
                  fs={18}
                  fw={400}
                  lh={28}
                  color="#fff"
                >
                  About Us
                </StyledText>
                <StyledText
                  className="mb-px"
                  fs={18}
                  fw={400}
                  lh={28}
                  color="#fff"
                >
                  Contact Us
                </StyledText>
                <StyledText
                  className="mb-px"
                  fs={18}
                  fw={400}
                  lh={28}
                  color="#fff"
                >
                  User Reviews
                </StyledText>
              </div>
              <div className="w-1/5">
                <StyledText
                  className="mb-6"
                  fs={20}
                  fw={800}
                  lh={36}
                  ls={2}
                  color="#fff"
                >
                  Nurses
                </StyledText>
                <StyledText
                  className="mb-px"
                  fs={18}
                  fw={400}
                  lh={28}
                  color="#fff"
                >
                  Find Job
                </StyledText>
                <StyledText
                  className="mb-px"
                  fs={18}
                  fw={400}
                  lh={28}
                  color="#fff"
                >
                  Account
                </StyledText>
                <StyledText
                  className="mb-px"
                  fs={18}
                  fw={400}
                  lh={28}
                  color="#fff"
                >
                  Data
                </StyledText>
                <StyledText
                  className="mb-px"
                  fs={18}
                  fw={400}
                  lh={28}
                  color="#fff"
                >
                  My Earning
                </StyledText>
              </div>
              <div className="w-1/5">
                <StyledText
                  className="mb-6"
                  fs={20}
                  fw={800}
                  lh={36}
                  ls={2}
                  color="#fff"
                >
                  Employer
                </StyledText>
                <StyledText
                  className="mb-px"
                  fs={18}
                  fw={400}
                  lh={28}
                  color="#fff"
                >
                  Find Nurses
                </StyledText>
                <StyledText
                  className="mb-px"
                  fs={18}
                  fw={400}
                  lh={28}
                  color="#fff"
                >
                  Account
                </StyledText>
                <StyledText
                  className="mb-px"
                  fs={18}
                  fw={400}
                  lh={28}
                  color="#fff"
                >
                  My Data
                </StyledText>
                <StyledText
                  className="mb-px"
                  fs={18}
                  fw={400}
                  lh={28}
                  color="#fff"
                >
                  Experience
                </StyledText>
              </div>
              <div className="w-2/5 ml-4 xl:ml-0 pr-4">
                <StyledText
                  className="mb-6"
                  fs={20}
                  fw={800}
                  lh={36}
                  ls={2}
                  color="#fff"
                >
                  Contact information
                </StyledText>
                <StyledText fs={20} fw={800} lh={35} ls={2} color="#fff">
                  Address
                </StyledText>
                <StyledText
                  className="mb-5"
                  fs={18}
                  fw={400}
                  lh={28}
                  color="#fff"
                >
                  Lorem ipsum dolor sit amet, consetetur
                </StyledText>
                <StyledText fs={20} fw={800} lh={35} ls={2} color="#fff">
                  Phone
                </StyledText>
                <StyledText
                  className="mb-5"
                  fs={24}
                  fw={400}
                  lh={37}
                  color="#fff"
                >
                  +34 534 534 455
                </StyledText>
                <StyledText fs={20} fw={800} lh={35} ls={2} color="#fff">
                  Email
                </StyledText>
                <StyledText
                  className="mb-5"
                  fs={24}
                  fw={400}
                  lh={37}
                  color="#fff"
                >
                  minai@gmail.com
                </StyledText>
              </div>
            </div>
            <form className="w-full lg:w-4/12" onSubmit={handleSubmit(submitForm)}>
              <StyledText
                className="mb-6"
                fs={20}
                fw={800}
                lh={36}
                ls={2.5}
                color="#fff"
              >
                Schedule A Free Consultation
              </StyledText>
              <div className="w-full mb-5">
                <HookFooterInput
                  name="email"
                  control={control}
                  errors={errors}
                  label="name@mail.com"
                  StartIcon={<MailIcon />}
                />
              </div>
              <div className="w-full flex mb-5">
                <div className="w-6/12 pr-2">
                  <HookFooterInput
                    name="name"
                    control={control}
                    errors={errors}
                    label="Denis"
                    StartIcon={<UserIcon />}
                  />
                </div>
                <div className="w-6/12 pl-2">
                  <HookFooterInput
                    name="purpose"
                    control={control}
                    errors={errors}
                    label="Purpose"
                    StartIcon={<PurposeIcon />}
                  />
                </div>
              </div>
              <div className="w-full mb-5">
                <HookFooterInput
                  name="message"
                  control={control}
                  errors={errors}
                  label="Write here"
                  StartIcon={<MessageIcon />}
                />
              </div>
              <div className="w-full flex items-center justify-end">
                <StyledButton type="submit" className="px-20 py-4" bg="#fff">
                  <StyledText fs={24} fw={400} lh={37} color="#DA3B19">
                    Send
                  </StyledText>
                </StyledButton>
              </div>
            </form>
          </div>
          <div className="w-7/12">
            <StyledText
              className="mb-4"
              fs={20}
              fw={800}
              lh={35}
              ls={2}
              color="#fff"
            >
              About
            </StyledText>
            <StyledText fs={18} fw={400} lh={28} color="#fff">
              Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam
              nonumy eirmod tempor invidunt ut labore et dolore magna aliquyam
              erat, sed diam voluptua. At vero eos et accusam et justo duo
              dolores et ea rebum. Stet clita kasd gubergren, no sea takimata
              sanctus est Lorem ipsum dolor sit amet. Lorem ipsum dolor sit
              amet, consetetur
            </StyledText>
          </div>
        </div>
      </MainContainer>
      <SubContainer>
        <div className="w-full h-full flex items-center justify-between container mx-auto">
          <StyledText color="#FFFFFF" fs={20} lh={29} fw={400}>
            All Rights Reserved by CaringDay
          </StyledText>
          <div className="flex items-center">
            <span className="cursor-pointer">
              <TwitterIcon />
            </span>
            <span className="cursor-pointer mx-12">
              <YouTubeIcon />
            </span>
            <span className="cursor-pointer">
              <LinkedInIcon />
            </span>
          </div>
        </div>
      </SubContainer>
    </div>
  );
};

export default Footer;

const MainContainer = styled.div`
  background-image: url('images/footer-bg.png');
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: white;
  backdrop-filter: blur(4px);
  overflow: hidden;
`;

const Overlay = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  backdrop-filter: blur(6px);
  background-color: rgba(0, 0, 0, 0.31);
`;

const SubContainer = styled.div`
  width: 100%;
  background-color: #da3b19;
  height: 88px;
`;
